


































































































































































































































































































































@font-face {
  font-family: "some_time_laterregular";
  src: url("../assets/fonts/some_time_later-webfont.woff2") format("woff2"),
    url("../assets/fonts/some_time_later-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.TimeCard_Container {
  max-width: 700px;
  margin: 0 auto;
}
.TimeCard {
  position: relative;
  overflow: hidden;
  height: 0;
  width: 100%;
  padding-top: 75%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  .TimeCard_Text {
    position: absolute;
    top: 50%;
    left: 50%;
    font-family: "some_time_laterregular";
    line-height: 85%;
    font-size: 120px;
    white-space: nowrap;

    .Number,
    .Unit,
    .Later {
      &:first-letter {
        font-size: 200%;
      }
      font-variant: small-caps;
    }
  }
}
[data-responsive="1"] {
  .TimeCard_Text {
    font-size: 110px;
  }
}
[data-responsive="2"] {
  .TimeCard_Text {
    font-size: 96px;
  }
}
[data-responsive="3"] {
  .TimeCard_Text {
    font-size: 80px;
  }
}
[data-responsive="4"] {
  .TimeCard_Text {
    font-size: 60px;
  }
}
[data-responsive="5"] {
  .TimeCard_Text {
    font-size: 42px;
  }
}
[data-responsive="6"] {
  .TimeCard_Text {
    font-size: 32px;
  }
}
