








































































































































































.App {
  overflow: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-family: sans-serif;
  text-align: center;
  background-image: url("~@/assets/main_bg.jpg");
  background-size: 800px 425px;
  color: white;
}
.AppContainer {
  box-sizing: border-box;
  max-width: 800px;
  margin: 20px auto;
  padding: 10px;

  background: rgba(255, 255, 255, 0.35);

  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
}

.DivisionLine {
  height: 1px;
  margin: 10px 10px;
  background: white;
  opacity: 0.15;
}

.Logo {
  width: 300px;
  max-width: 100%;
}
.Header {
  font-size: 24px;
  text-transform: uppercase;
  color: rgb(23, 30, 54);
}

.Controls {
  padding: 20px;
  border-radius: 20px;
  text-align: left;
  input,
  select {
    cursor: pointer;
    outline: none;
    box-shadow: none;
    border: none;
    font-size: 14px;
  }
  .ControlSection {
    display: inline-block;
    margin-bottom: 20px;
    margin-right: 20px;
    text-align: left;
    .ControlLabel {
      display: block;
      margin-right: 10px;
      margin-bottom: 2.5px;
      font-weight: bolder;
    }
  }
}
.SaveButton {
  cursor: pointer;
  border-radius: 5px;
  font-size: 12px;
  padding: 10px;
  margin: 10px 0;
  background: rgb(13, 108, 204);
  box-shadow: -4px 4px rgb(6, 57, 107);
  border: none;
  outline: none;
  text-transform: uppercase;
  font-weight: bolder;
  color: white;
}

input,
select {
  border-radius: 4px;
  padding: 10px;
}
